// Colors
$blue:#00519C;
$white:#fff;
$black:#000;
$ice:#EDF8FF;
// /Colors

// Common
.title-h3{
  font-weight: 300;
  font-size: 36px;
  line-height: 44px;
  text-align: center;
  padding: 50px 0 12px 0;
  color: $blue;
}
.wrapper{
  max-width: 1140px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 10px;
  padding-left: 10px;
}
body{
  font-family: 'Montserrat', sans-serif;
}
// /Common
nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo{
  display: flex;
  align-items: center;
  text-decoration: none;
  img{
    margin-right: 20px;
  }
}
.nav-list{
  display: flex;
  &__item{
    margin-left: 20px;
    font-weight: 600;
    font-size: 13px;
    line-height: 17px;
    transition: 0.3s;
    &:first-child{
      margin-left: 0;
    }
    &:hover{
      transform: translateY(-1px);
    }
  }
}
.nav-link{
  color: $blue;
  text-decoration: none;
}

.name-logo{
  font-weight: 900;
  color: $blue;
  font-size: 20px;
  line-height: 19px;
  max-width: 190px;
}

.subname-logo{
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
}

// title
.title{
  margin-top: 30px;
  background: url(../../../img/trout-bg.jpg) 50% 50%/cover no-repeat;
  padding: 30px 0 70px 0;
}
// /title

// receipes
.ingridients{
  padding-bottom: 40px;
}
.receipes-img{
  display: block;
  width: 100%;
}
.col-1{
  display: flex;
  flex-direction: column;
}
.cols-inner{
  display:grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 100px;
  font-weight: 500;
  font-size: 14px;
  line-height: 26px;
}

.title-h4{
  font-size: 24px;
  line-height: 29px;
  color: $blue;
  margin-top: 30px;
  margin-bottom: 12px;
  font-weight: 500;
}
.accent{
  font-weight: 600;
}
.accent-blue{
  color: $blue;
}
.method{
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  padding-bottom: 50px;
}
.step-title{
  font-weight: 600;
  font-size: 20px;
}
.step-text{
  margin-bottom: 30px;
}
// /receipes

//footer
.footer{
  background-color: $ice;
  padding: 45px 0;
}

.footer-inner{
  display:grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap: 100px;
}
.footer-item{
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.logo-name{
  font-size: 20px;
  line-height: 19px;
  color: $blue;
  font-weight: 900;
}
.kosher-name{
  font-weight: 600;
  font-size: 13px;
  line-height: 30px;
  color: #000000;
}
.footer-text{
  font-size: 13px;
  line-height: 30px;
}
// /footer
.hamburger-menu{
  display: none;
}
#menu__toggle {
  opacity: 0;
}

#menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}
#menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
}
#menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}
#menu__toggle:checked ~ .menu__box {
  visibility: visible;
  left: 0;
}

.menu__btn {
  display: flex;
  align-items: center;
  position: absolute;
  top: 20px;
  left: 10px;

  width: 26px;
  height: 26px;

  cursor: pointer;
  z-index: 1;
}

.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
  display: block;
  position: absolute;

  width: 100%;
  height: 2px;

  background-color: $blue;

  transition-duration: .25s;
}
.menu__btn > span::before {
  content: '';
  top: -8px;
}
.menu__btn > span::after {
  content: '';
  top: 8px;
}

.menu__box {
  display: block;
  position: fixed;
  visibility: hidden;
  top: 0;
  left: -100%;

  width: 300px;
  height: 100%;

  margin: 0;
  padding: 80px 0;

  list-style: none;

  background-color: #ECEFF1;
  box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);

  transition-duration: .25s;
}
.menu__item {
  display: block;
  padding: 9px 9px;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  transition-duration: .25s;
}
.menu__item:hover {
  background-color: #CFD8DC;
}
